<template>
  <div>

    <!-- Add Notification Modal -->
<div
        class="modal fade"
        id="AddNotificationModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Add Notification & Updates
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="AddNotification">
                  <div class="form-group">
                    <label for="ntitle">Notification Title</label>
                    <input
                      type="text"
                      id="ntitle"
                      name="ntitle"
                      placeholder="Enter Notification Title"
                      v-model="ntitle"
                      class="form-control"
                      required
                    />
                  </div>
                  <div class="form-group">
                    <label for="nlink">Notification link</label>
                    <input
                      type="text"
                      id="nlink"
                      name="nlink"
                      placeholder="Enter Notification link if any or write (No link)"
                      v-model="nlink"
                      class="form-control"
                    />
                  </div>


                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal"
>Add </button>
                    <button class="btn btn-success mt-2 ms-2" type="reset">Clear</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

    
    <!-- Update Modal -->

      <div
        class="modal fade"
        id="UpdateNotificationModal"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title text-center" id="exampleModalLabel">
                Update Notification
              </h5>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <!-- modal body -->
            <div class="modal-body">
              <div class="card-body">
                <form @submit.prevent="UpdateNotification">
                  <div class="form-group">
                    <label for="untitle">Notification title</label>
                    <input
                      type="text"
                      id="untitle"
                      name="untitle"
                      placeholder="Enter Notification title"
                      v-model="NotificationTitle"
                      class="form-control"
                    />
                
                  </div>
                  <div class="form-group">
                    <label for="unlink">Notification Link</label>
                    <input
                      type="text"
                      id="unlink"
                      name="unlink"
                      placeholder="Enter Notification Link"
                      v-model="NotificationLink"
                      class="form-control"
                    />
                  </div>


                  <button class="btn btn-success mt-2 " data-bs-dismiss="modal">Update</button>
                </form>
              </div>
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>

    <button
      class="btn btn-success mt-5"
      data-bs-toggle="modal"
      data-bs-target="#AddNotificationModal"
    >
      Add Notification Updates
    </button>    

<div class="mt-4">
<div class="table-responsive mt-4">
    <table class="table table-striped table-sm">
        <thead>
            <tr class="text-center fw-bold">
            <th scope="col">Date</th>
            <th scope="col">Notification</th>
            <th scope="col">Notification Link</th>
            <th scope="col">Update</th>
            <th scope="col">Delete</th>
            </tr>
            </thead>
            <tbody class="text-center ">
                <tr class="mt-2" v-for="(notification, _id) in Notifications" :key="_id">

                <td>{{notification.ndate.split("T")[0]}}</td>
                <td>{{notification.ntitle}}</td>
                <td><a :href="notification.nlink" target="_blank"><button class="btn btn-info">Open Link</button></a></td>
                <td><button class="btn btn-primary" data-bs-toggle="modal"
      data-bs-target="#UpdateNotificationModal" @click="updateNotification(_id)">Update</button></td>
                <td><button class="btn btn-danger" @click="deleteNotification(_id)">Delete</button></td>
                
                </tr>
                
            </tbody>
    </table>
</div>
</div>




  </div>
</template>

<script>
import axios from "axios";
import config from "@/config"

export default {
name:'AddNotice',
data(){
  return{
    ntitle:"",
    nlink:"",
    Notifications:[],
    NotificationTitle:"",
    NotificationLink:"",


  }
},
methods:{
  async AddNotification(){

let notification = {
          ntitle:this.ntitle,
    nlink:this.nlink
      }
let res = await axios.post(`${config.apiBaseUrl}/api/notifications/add-notification`,notification)
      
      if(res.data.success){
        this.$toasted.show('Notification Added');
        console.log('Notification added')
        this.getNotifications()
        }
        
},

// Get notification lists

async getNotifications(){
  
    let {
    data
  } = await axios.get(`${config.apiBaseUrl}/api/notifications/get-notification`)
  this.Notifications = data
 

},

// Update Notification
updateNotification(_id){
  this._id=this.Notifications[_id]._id
  this.NotificationTitle = this.Notifications[_id].ntitle
  this.NotificationLink = this.Notifications[_id].nlink
 
},

async UpdateNotification(){

  let notification = {
    _id:this._id,
    ntitle: this.NotificationTitle,
    nlink: this.NotificationLink,
    }

    let res = await axios.patch(`${config.apiBaseUrl}/api/notifications/update-notification`,notification)
      this.getNotifications() //calling gte notification
        
                this.$toasted.show('Notification Updated');

      if(res.data.success){
          console.log('add something here')
        }
        return res;


},


// Delete Notification
async deleteNotification(_id){
      
       let id  = this.Notifications[_id]._id
      let res = await axios.delete(`${config.apiBaseUrl}/api/notifications/delete-notification`,{data: { _id:id} })
  
  this.getNotifications() //calling get student
                this.$toasted.show('Notification Deleted');

      if(res.data.success){
          console.log('add something here')
        }
        return res;
     }



},
created(){
  this.getNotifications()
}
}</script>

<style scoped>
a{
  color:white;
}
</style>